@import url("https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@300&display=swap");

body {
  background-color: #f2f4f6;
  color: #17223b;
  text-align: center;
  margin: 0;
  font-family: "Roboto Condensed";
}

.container {
  box-sizing: border-box;
  max-width: 30%;
  width: 100%;
  margin: 0 auto;
  padding: 20px;
}

.row {
  display: flex;
}

input {
  border: none;
  border-radius: 0.4rem;
  width: 100%;
  padding: 10px 6px;
  font-size: 16px;
  font-weight: 600;
  text-transform: uppercase;
  text-align: center;
  box-shadow: 0px 0px 25px 1px rgba(50, 50, 50, 0.1);
}

input:focus {
  outline: none;
}

.card {
  display: flex;
  justify-content: center;
}

.card-container {
  margin: 20px 5px;
  border-radius: 20px;
  position: absolute;
  width: 450px;
  height: 450px;
  background-color: #fff;
  box-shadow: 0px 0px 25px 1px rgba(50, 50, 50, 0.1);
}

.city-name {
  font-weight: 300;
  font-size: 40px;
  color: #6b778d;
  bottom: 0;
}

.icon {
  position: relative;
  width: 300px;
  height: 300px;
  bottom: 280px;
  float: right;
  margin: 5px 5px 0 0;
}

.temp {
  position: relative;
  font-weight: 300;
  font-size: 80px;
  color: #17223b;
  right: 120px;
}

.card-footer {
  background-color: #4361EE;
  color: aliceblue;
  position: relative;
  bottom: 280px;
  border-radius: 0px 0px 20px 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
}

.temp-max-min {
  font-weight: 300;
  font-size: 50px;
  color: aliceblue;

}